import React, {Component, createContext} from "react"

export const AstronautsContext = createContext()

class AstronautsContextProvider extends Component {
	
	state = {
		astrosList: [],
		peopleAmount: 0,
		isLoading: false
	}

	handleErrors = (response) => {
		if (!response.ok) {
				throw Error(response.statusText);
		}
		this.props.setIsError(false)
		return response;
	}

	updateData = () => {
		fetch("https://api.open-notify.org/astros.json")
		.then(this.handleErrors)
		.then(res => res.json())
		.then(result => {
			let list = result.people.map((item, index) => {
				return (
					item.craft === "ISS" && [index, item.name]
				)
			})
			this.setState({
				astrosList: list,
				peopleAmount: list.length,
				isLoading: false
			})
		})
		.catch(error => {
			this.props.setIsError(true)
			console.log(error)
		})
	}

	componentDidMount() {
		this.setState({
			isLoading: true
		})
		this.updateData()
		this.interval = setInterval(() => this.updateData(), 5000)
	}

	componentWillUnmount() {
		clearInterval(this.interval)
	}

	render = () => {
		return (
			<AstronautsContext.Provider value={{...this.state}}>
				{this.props.children}
			</AstronautsContext.Provider> 
		)
	}
}

export default AstronautsContextProvider