import React, {Component} from "react"
import Astronaut from "./components/Astronaut"
import { AstronautsContext } from "../../contexts/AstronautsContext"

class Astros extends Component {
	static contextType = AstronautsContext
	render() {
		return (
			<AstronautsContext.Consumer>{(context) => {
				const {astrosList, peopleAmount, isLoading} = context
				const astronauts = astrosList.map(item => {
					return (
						<Astronaut key={item[0]} name={item[1]}/>
					)
				})
				return (
					<div className="astroBlock container">
						<ul className="astronautList">
							{isLoading ? "<h3>Loading data...</h3>" : astronauts}
						</ul>
						<div className="total">
							<span>Total amount: {peopleAmount} people on ISS</span>
						</div>
					</div>
				)
			}}
			</AstronautsContext.Consumer>
		)
	}
}

export default Astros